<template>
	<div v-if="!$loadingRouteData"
		class="show-post container">
		<h1>{{ post.title }}</h1>
		<h2>{{ post.summary }}</h2>
		<div class="markdown">
			{{{ post.content | markdown }}}
		</div>
	</div>

</template>

<script>
	import Api from 'src/api'

	export default {
		data() {
			return {
				post: {}
			}
		},

		route: {
			data({to}) {
				return Api.Post.showPost(to.params.id)
					.then(snapshot => {
						return {
							post: snapshot.val()
						}
					})
			}
		}

	}
</script>

<style lang="stylus">
	div.show-post
		padding-top 20px

</style>



