import Firebase from 'firebase'
import config from '../config'

firebase.initializeApp(config.firebase)
let rootRef = firebase.database()

export default {
  Post: {
		index () {
			return new Promise((resolve, reject) =>{
				rootRef.ref('/posts').on('value', snapshot => {
					resolve(snapshot)
				})
			})
		},

    createPost (post) {
			post.createdAt = firebase.database.ServerValue.TIMESTAMP
      return rootRef.ref('/posts').push(post)
    },

		showPost (id) {
			return new Promise((resolve, reject) =>{
				rootRef.ref(`/posts/${id}`).on('value', snapshot => {
					console.log(snapshot)
					resolve(snapshot)
				})
			})
		}

  }
}



