<template>
  <div class="create-post container">
    <form action="">
      <input v-model="post.title"
				class="title"
				type="text"
				placeholder="标题">

      <input v-model="post.summary"
				class="summary"
				type="text"
				placeholder="简介">

      <textarea v-model="post.content"
      	placeholder="内容, markdown 语法"></textarea>

      <a @click="savePost"
        class="btn btn-primary btn-radius">savePost</a>

    </form>
  </div>

</template>

<script>
  import Api from 'src/api'

  export default {
    data () {
			return {
				post: {
					title: '',
					summary: '',
					content: ''
				}
			}
    },

    methods: {
      savePost () {
        Api.Post.createPost(this.post)
					.then(res => {
						console.log(res);
						alert('Saved Success')
						this.$router.go({ name: 'showPost', params: { id: res.key }})
					})
      }
    }
  }
</script>

<style lang="stylus">
  @import '../assets/stylus/variables.styl'

  div.create-post
    padding-top 20px

    input,textarea
      background $bgColor
      border 0
      width 100%

    input
      margin 10px 0
      line-height 1.6
      &.title
        font-size 30px
      &.summary
        font-size 20px

    textarea
      height 400px

</style>



