<template>
  <div class="index container">
    <div class="content-wrapper">
      <div class="stream">
        <Post v-for="(key, post) in posts"
        	v-link="{ name: 'showPost', params: { id: key }}"
					:post="post"></Post>
      </div>
      <div class="sidebar-content">
        <Sidebar></Sidebar>
      </div>
    </div>
  </div>
</template>

<script>
  import Post from 'components/Post'
  import Sidebar from 'components/Sidebar'
	import Api from 'src/api'

  export default {
		data () {
			return {
				posts: []
			}
		},

		route: {
			data () {
				return Api.Post.index()
					.then( snapshot => {
						return {
							posts: snapshot.val()
						}
					})
			}
		},

    components: {
      Post,
      Sidebar
    },

    methods: {
      scroll () {
        console.log('obj');
      }
    }

  }
</script>

<style lang="stylus" scoped>
  div.index
    padding-top 20px
    div.content-wrapper
      position relative

      div.stream
        max-width 660px

      div.sidebar-content
        padding-left 20px
        width 320px
        position absolute
        top 0
        right 0
        bottom 0

</style>



